import React, { Suspense, lazy } from 'react';
import { Route } from 'react-router-dom';

const FormCategoria = lazy(() => import('../../../pages/Movimentos/Categorias/FormCategoria/FormCategoria'));
const FormCliente = lazy(() => import('../../../pages/Pessoas/Clientes/FormCliente/FormCliente'));
const FormFornecedor = lazy(() => import('../../../pages/Pessoas/Fornecedores/FormFornecedor/FormFornecedor'));
const FormFuncaoFuncionario = lazy(() => import('./../../../pages/Pessoas/FuncaoFuncionario/FormFuncaoFuncionario/FormFuncaoFuncionario'));
const FormFuncionario = lazy(() => import('../../../pages/Pessoas/Funcionarios/FormFuncionario/FormFuncionario'));
const FormMarca = lazy(() => import('./../../../pages/Movimentos/Marcas/FormMarca/FormMarca'));
const FormProduto = lazy(() => import('../../../pages/Movimentos/Produtos/FormProduto/FormProduto'));
const FormServico = lazy(() => import('../../../pages/Movimentos/Servicos/FormServico/FormServico'));
const FormTitular = lazy(() => import('../../../pages/Pessoas/Associados/FormTitular/FormTitular'));
const ListCategoria = lazy(() => import('./../../../pages/Movimentos/Categorias/ListCategoria/ListCategoria'));
const ListCidade = lazy(() => import('../../../pages/Cidades/Cidade/ListCidade/ListCidade'));
const ListCliente = lazy(() => import('../../../pages/Pessoas/Clientes/ListCliente/ListCliente'));
const ListCondicaoPagamento = lazy(() => import('../../../pages/CondicaoPagamento/CondicaoPagamento/ListCondicaoPagamento/ListCondicaoPagamento'));
const ListEstado = lazy(() => import('../../../pages/Cidades/Estado/ListEstado/ListEstado'));
const ListFornecedor = lazy(() => import('../../../pages/Pessoas/Fornecedores/ListFornecedor/ListFornecedor'));
const ListFuncaoFuncionario = lazy(() => import('./../../../pages/Pessoas/FuncaoFuncionario/ListFuncaoFuncionario/ListFuncaoFuncionario'));
const ListFuncionario = lazy(() => import('../../../pages/Pessoas/Funcionarios/ListFuncionario/ListFuncionario'));
const ListMarca = lazy(() => import('../../../pages/Movimentos/Marcas/ListCategoria/ListMarca'));
const ListPais = lazy(() => import('../../../pages/Cidades/Pais/ListPais/ListPais'));
const ListProduto = lazy(() => import('../../../pages/Movimentos/Produtos/ListProduto/ListProduto'));
const ListServico = lazy(() => import('./../../../pages/Movimentos/Servicos/ListServico/ListServico'));
const ListTitular = lazy(() => import('../../../pages/Pessoas/Associados/ListTitular/ListTitular'));
const ListUser = lazy(() => import('../../../pages/Pessoas/Users/ListUser/ListUser'));
const FormPais = lazy(() => import('../../../pages/Cidades/Pais/FormPais/FormPais'));
const ListUnidadeMedida = lazy(() => import('./../../../pages/Movimentos/UnidadeMedida/ListUnidadeMedida/ListUnidadeMedida'));
const FormUnidadeMedida = lazy(() => import('./../../../pages/Movimentos/UnidadeMedida/FormUnidadeMedida/FormUnidadeMedida'));
const FormCidade = lazy(() => import('../../../pages/Cidades/Cidade/FormCidade/FormCidade'));
const FormEstado = lazy(() => import('../../../pages/Cidades/Estado/FormEstado/FormEstado'));
const FormUser = lazy(() => import('../../../pages/Pessoas/Users/FormUser/FormUser'));
const FormCondicaoPagamento = lazy(() => import('../../../pages/CondicaoPagamento/CondicaoPagamento/FormCondicaoPagamento/FormCondicaoPagamento'));
const ListFormaPagamento = lazy(() => import('../../../pages/CondicaoPagamento/FormPagamento/ListFormaPagamento/ListFormaPagamento'));
const FormFormaPagamento = lazy(() => import('../../../pages/CondicaoPagamento/FormPagamento/FormFormaPagamento/FormFormaPagamento'));

const RoutePath: React.FC = () => {

    return (
        <>
            <Suspense fallback={<div>Loading...</div>}>

                <Route path="/categoria" component={ListCategoria} exact />
                <Route path="/categoria/edit/:id" component={FormCategoria} />
                <Route path="/categoria/new" component={FormCategoria} />

                <Route path="/cidade" component={ListCidade} exact />
                <Route path="/cidade/edit/:id" component={FormCidade} />
                <Route path="/cidade/new" component={FormCidade} />

                <Route path="/cliente" component={ListCliente} exact />
                <Route path="/cliente/edit/:id" component={FormCliente} />
                <Route path="/cliente/new" component={FormCliente} />

                <Route path="/condicao-pagamento" component={ListCondicaoPagamento} exact />
                <Route path="/condicao-pagamento/edit/:id" component={FormCondicaoPagamento} />
                <Route path="/condicao-pagamento/new" component={FormCondicaoPagamento} />

                <Route path="/estado" component={ListEstado} exact />
                <Route path="/estado/edit/:id" component={FormEstado} />
                <Route path="/estado/new" component={FormEstado} />

                <Route path="/forma-pagamento" component={ListFormaPagamento} exact />
                <Route path="/forma-pagamento/edit/:id" component={FormFormaPagamento} />
                <Route path="/forma-pagamento/new" component={FormFormaPagamento} />

                <Route path="/fornecedor" component={ListFornecedor} exact />
                <Route path="/fornecedor/edit/:id" component={FormFornecedor} />
                <Route path="/fornecedor/new" component={FormFornecedor} />

                <Route path="/funcao-funcionario" component={ListFuncaoFuncionario} exact />
                <Route path="/funcao-funcionario/edit/:id" component={FormFuncaoFuncionario} />
                <Route path="/funcao-funcionario/new" component={FormFuncaoFuncionario} />

                <Route path="/funcionario" component={ListFuncionario} exact />
                <Route path="/funcionario/edit/:id" component={FormFuncionario} />
                <Route path="/funcionario/new" component={FormFuncionario} />

                <Route path="/marca" component={ListMarca} exact />
                <Route path="/marca/edit/:id" component={FormMarca} />
                <Route path="/marca/new" component={FormMarca} />

                <Route path="/pais" component={ListPais} exact />
                <Route path="/pais/edit/:id" component={FormPais} />
                <Route path="/pais/new" component={FormPais} />

                <Route path="/produto" component={ListProduto} exact />
                <Route path="/produto/edit/:id" component={FormProduto} />
                <Route path="/produto/new" component={FormProduto} />

                <Route path="/servico" component={ListServico} exact />
                <Route path="/servico/edit/:id" component={FormServico} />
                <Route path="/servico/new" component={FormServico} />

                <Route path="/titular" component={ListTitular} exact />
                <Route path="/titular/edit/:id" component={FormTitular} />
                <Route path="/titular/new" component={FormTitular} />

                <Route path="/user" component={ListUser} exact />
                <Route path="/user/edit/:id" component={FormUser} />
                <Route path="/user/new" component={FormUser} />

                <Route path="/unidade-medida" component={ListUnidadeMedida} exact />
                <Route path="/unidade-medida/edit/:id" component={FormUnidadeMedida} />
                <Route path="/unidade-medida/new" component={FormUnidadeMedida} />

            </Suspense>
        </>
    );
}

export default RoutePath;
